export class Corrida {
    
    constructor(corrida, horaLargada) {
        this._corrida = corrida;
        this._horaLargada = horaLargada;
        this._voltas = [];
        
        Object.freeze(this);
    }
    
    get corrida() {
        return this._corrida;
    }

    get voltas() {
        
        return [].concat(this._voltas);
    }
    
    get horaLargada() {
        return this._horaLargada;
    }

    adicionaVolta(volta) {
        
        this._voltas.push(volta);
    }
}
